#About{
    background-color: #ffffff;
    min-height: 100vh;
    font-family: 'Text';

    h2{
        color: #ececec;
        font-size: 14vw;
        font-family: Handwriting;

        @media (max-width: 575.98px) {
            font-size: 24vw;
        }
        @media (max-width: 767.98px) {
            font-size: 24vw;
        }
        @media (max-width: 991.98px) {
            font-size: 20vw;
        }
        @media (max-width: 1199.98px) {
            font-size: 20vw;
        }
        @media (max-width: 1399.98px) {
            font-size: 16vw;
        }
    }

    p{
        position:relative;
        top: -20vh;
        left: 40vh;
        width: 120vh;
        font-size: 2vh;
        text-align: justify;

        span{
            font-weight: bold;
        }

        i{
            text-decoration: underline;
        }

        @media (max-width: 1399.98px) {
            left: 200px;
            width: 75%;
            top: -18vh;
            font-size: 1.5vh;
        }
        @media (max-width: 1199.98px) {
            left: 200px;
            width: 75%;
            top: -16vh;
            font-size: 1.5vh;
        }
        @media (max-width: 991.98px) {
            left: 0;
            width: 100%;
            top: -4vh;
            font-size: 1.5vh;
        }
        @media (max-width: 767.98px) {
            left: 0;
            width: 100%;
            top: -4vh;
            font-size: 1.5vh;
        }
        @media (max-width: 575.98px) {
            left: 0;
            width: 100%;
            top: -4vh;
            font-size: 1.5vh;
        }
        
        
        
        
    }

    .content{
        #aboutButton{
            position: absolute;
            width: 100px;
            left: 50%;
            margin-left: -50px;
        }
    }
}