@font-face {
  font-family: "Handwriting";
  src: url(./Fonts/Dancing/DancingScript-Bold.ttf);
}
@font-face {
  font-family: "Titles";
  src: url(./Fonts/Poppins/Poppins-Bold.ttf);
}
@font-face {
  font-family: "SubTitles";
  src: url(./Fonts/Poppins/Poppins-Medium.ttf);
}
@font-face {
  font-family: "Text";
  src: url(./Fonts/Poppins/Poppins-ExtraLight.ttf);
}
html body h1 {
  color: #ffffff;
}
html body h2 {
  color: #ffffff;
}
html body header {
  height: 100vh;
}
html body header .content {
  width: 100vh;
  position: relative;
}
html body header .content h1 {
  font-family: "Handwriting";
  font-size: 5vw;
  line-height: 0.7;
  text-align: center;
  color: #000000;
  letter-spacing: 0;
  /* -webkit-text-stroke: 1px #000000; */
}
@media (max-width: 1399.98px) {
  html body header .content h1 {
    font-size: 6vw;
  }
}
@media (max-width: 1199.98px) {
  html body header .content h1 {
    font-size: 8vw;
  }
}
@media (max-width: 991.98px) {
  html body header .content h1 {
    font-size: 10vw;
  }
}
@media (max-width: 767.98px) {
  html body header .content h1 {
    font-size: 12vw;
  }
}
@media (max-width: 575.98px) {
  html body header .content h1 {
    font-size: 12vw;
  }
}
html body header .content p {
  position: relative;
  z-index: 1;
  font-family: Text;
  width: 100%;
  font-size: 2vw;
  text-shadow: none;
  color: yellowgreen;
  text-align: center;
}
@media (max-width: 575.98px) {
  html body header .content p {
    font-size: 4vw;
  }
}
@media (max-width: 767.98px) {
  html body header .content p {
    font-size: 4vw;
  }
}
@media (max-width: 991.98px) {
  html body header .content p {
    font-size: 4vw;
  }
}
html body header .content #appButton {
  position: absolute;
  bottom: -100px;
  width: 100px;
  left: 50%;
  margin-left: -50px;
}
html .nextButton {
  font-family: "Handwriting";
  font-size: 2vh;
}
html .nextButton a {
  text-decoration: none;
  color: inherit;
}
html .btn-outline-secondary {
  color: #ffffff;
  border-color: #ffffff;
  background-color: #000000;
}
html .btn-outline-secondary:hover {
  color: #000000;
  border-color: #000000;
  background-color: #ffffff;
}

