nav{
    background-color: #000000;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    opacity: 0;

    .logo{
        width: 200px;
        color: #ffffff;
        font-weight: bold;
        
        a{
            color: #ffffff;
            text-decoration: none;
            font-family: "Handwriting";
            letter-spacing: 1px;
            font-size: 1vw;
            transition: 0.3s ease-in-out;

            @media (max-width: 1399.98px) {
                font-size: 1.5vw;
            }
            @media (max-width: 1199.98px) {
                font-size: 2vw;
            }
            @media (max-width: 991.98px) {
                font-size: 2.5vw;
            }
            @media (max-width: 767.98px) {
                font-size: 3vw;
            }
            @media (max-width: 575.98px) {
                font-size: 4vw;
            }

            &:hover{
                color: greenyellow;
            }
        }
    }

    .btn-gb{
        position: relative;
        width: 40px;
        height: 24px;
        padding: 0;
        border: none;
        z-index: 100;

        &:active{
            background-color: #000000;

            .buttonLine{
                background-color: #000000;
            }
        }

        .buttonLine{
            display: block;
            width: 20px;
            height: 1px;
            background-color: #ffffff;
            position: relative;
            top: -2px;
            left: 10px;
            margin-top: 4px;
            border-radius: 5px;
        }

        &:hover{
            .buttonLine{
                background-color: #000000;
            }
        }
    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        li{
            padding-left: 15px;
            padding-right: 15px;
            display: inline-block;
            a{
                text-decoration: none;
                color: #ffffff;
                font-weight: bold;
                font-family: SubTitles;
                letter-spacing: 1px;
                transition: 0.3s ease-in-out;

                &:hover{
                    color: greenyellow;
                    text-shadow: 0 0 1px greenyellow;
                }
            }
            .active{
                color: greenyellow;
            }
        }
    }

    .toggleList{
        margin: 0;
        padding: 0;
        margin-top: 40px;
        list-style: none;

        li{
            padding-left: 15px;
            padding-right: 15px;
            display: block;
            text-align: center;
        }
    }
}