#Web{
    background-color: #ffffff;
    min-height: 100vh;

    h2{
        color: #e7e7e7;
        font-size: 10vw;
        font-family: Handwriting;

        @media (max-width: 1399.98px) {
            font-size: 10vw;
        }
        @media (max-width: 1199.98px) {
            font-size: 10vw;
        }
        @media (max-width: 991.98px) {
            font-size: 12vw;
        }
        @media (max-width: 767.98px) {
            font-size: 14vw;
        }
        @media (max-width: 575.98px) {
            font-size: 14vw;
        }
        
    }

    .webText{
        position:relative;
        font-family: "Text";
        top: -12vh;
        left: 25vh;
        width: 140vh;
        font-size: 4vh;
        text-align: justify;

        @media (max-width: 1399.98px) {
            top: -8vh;
            left: 16vh;
            width: 80vh;
            font-size: 3vh;
        }
        @media (max-width: 1199.98px) {
            top: -6vh;
            left: 16vh;
            width: 80vh;
            font-size: 3vh;
        }
        @media (max-width: 991.98px) {
            top: -6vh;
            left: 14vh;
            width: 80vh;
            font-size: 3vh;
        }
        @media (max-width: 767.98px) {
            top: -5vh;
            left: 0;
            width: 100vh;
            text-align: center;
            font-size: 3vh;
        }
        @media (max-width: 575.98px) {
            top: -4vh;
            left: 0;
            width: 100vh;
            text-align: center;
            font-size: 2vh;
        }
        
    }

    .item{
        h3{
            font-family: SubTitles;
            font-size: 2vw;
            text-align: center;

            @media (max-width: 991.98px) {
                font-size: 2.5vw;
            }
            @media (max-width: 767.98px) {
                font-size: 3vw;
            }
            @media (max-width: 575.98px) {
                font-size: 4vw;
            }
        }

        p{
            text-align: center;
            font-family: "Text";

            @media (max-width: 575.98px) {
                font-size: 3vw;
            }
        }
    }

    .content{
        #WebButton{
            position: absolute;
            width: 150px;
            left: 50%;
            margin-left: -75px;
        }
    }
}