#Contact{
    background-color: #ffffff;
    min-height: 90vh;

    h2{
        color: #e7e7e7;
        font-size: 10vw;
        font-family: Handwriting;
        text-align: center;

        @media (max-width: 1399.98px) { 
            font-size: 12vw;
        }
        @media (max-width: 1199.98px) {
            font-size: 12vw;
        }
        @media (max-width: 991.98px) {
            font-size: 15vw;
        }
        @media (max-width: 991.98px) {
            font-size: 20vw;
        }
        @media (max-width: 767.98px) {
            font-size: 20vw;
        }
        @media (max-width: 575.98px) {
            font-size: 24vw;
        }
    }

    h4{
        font-family: "SubTitles";
    }

    .contactText{
        position:relative;
        font-family: "Text";
        top: -10vh;
        left: 20vh;
        width: 140vh;
        font-size: 4vh;
        text-align: center;

        @media (max-width: 1399.98px) { 
            top: -8vh;
            width: 140vh;
        }
        @media (max-width: 1199.98px) {
            top: -8vh;
            width: 140vh;
        }
        @media (max-width: 991.98px) {
            top: -10vh;
            width: 100vh;
        }
        @media (max-width: 767.98px) {
            top: -8vh;
            left: 0;
            width: 100vh;
        }
        @media (max-width: 575.98px) {
            top: -8vh;
            left: 0;
            width: 100vh;
        }
    }

    p{
        position: relative;
        font-family: "Text";
        text-align: justify;
        font-size: 1vw;

        @media (max-width: 1399.98px) {
            font-size: 1.2vw;
        }
        @media (max-width: 1199.98px) {
            font-size: 1.5vw;
        }
        @media (max-width: 991.98px) {
            top: 0;
            font-size: 2vw;
        }
        @media (max-width: 767.98px) {
            top: 0;
            font-size: 2.5vw;
        }
        @media (max-width: 575.98px) {
            font-size: 3vw;
            top: 0;
        }

        span{
            @media (max-width: 1199.98px) {
                font-size: 1.5vw;
            }
            @media (max-width: 767.98px) {
                top: 0;
                font-size: 2.5vw;
            }
            @media (max-width: 575.98px) {
                font-size: 3vw;
                top: 0;
            }
        }
    }

    form{
        .form-label{
            font-size: 1vw;
            font-family: SubTitles;

            @media (max-width: 1399.98px) {
                font-size: 1.4vw;
            }
            @media (max-width: 1199.98px) {
                font-size: 1.5vw;
            }
            @media (max-width: 991.98px) {
                font-size: 2vw;
            }
            @media (max-width: 767.98px) {
                font-size: 3vw;
            }
            @media (max-width: 575.98px) {
                font-size: 4vw;
            }
        }

        button{
            font-family: "Handwriting";
            font-size: 2vh;
        }

        p.error{
            color: red;
            font-size: 12px;
        }

        h3{
            font-family: "Handwriting";
            font-size: 2vw;

            @media (max-width: 1399.98px) {
                font-size: 2.5vw;
            }
            @media (max-width: 1199.98px) {
                font-size: 3vw;
            }
            @media (max-width: 991.98px) {
                font-size: 4vw;
            }
            @media (max-width: 767.98px) {
                font-size: 5vw;
            }
            @media (max-width: 575.98px) {
                font-size: 6vw;
            }
        }
    }
}