#Projects {
  min-height: 100vh;
  font-family: "Text";
  padding-left: 15px;
  padding-right: 15px;
}
#Projects h2 {
  color: yellowgreen;
  font-size: 10vw;
  font-family: "Handwriting";
}
@media (max-width: 575.98px) {
  #Projects h2 {
    font-size: 22vw;
  }
}
#Projects .contenItem {
  background-color: #000000;
  border-radius: 10px;
  padding: 10px;
  position: relative;
}
#Projects .contenItem b {
  color: yellowgreen;
  font-size: 1vw;
  font-family: "SubTitles";
  transition: 0.3s ease-in-out;
}
@media (max-width: 1399.98px) {
  #Projects .contenItem b {
    font-size: 1.5vw;
  }
}
@media (max-width: 1199.98px) {
  #Projects .contenItem b {
    font-size: 1.5vw;
  }
}
@media (max-width: 991.98px) {
  #Projects .contenItem b {
    font-size: 2vw;
  }
}
@media (max-width: 767.98px) {
  #Projects .contenItem b {
    font-size: 4vw;
  }
}
@media (max-width: 575.98px) {
  #Projects .contenItem b {
    font-size: 4vw;
  }
}
#Projects .contenItem span {
  color: #ffffff;
  font-size: 0.8vw;
}
@media (max-width: 1399.98px) {
  #Projects .contenItem span {
    font-size: 1vw;
  }
}
@media (max-width: 1199.98px) {
  #Projects .contenItem span {
    font-size: 1.2vw;
  }
}
@media (max-width: 991.98px) {
  #Projects .contenItem span {
    font-size: 2vw;
  }
}
@media (max-width: 767.98px) {
  #Projects .contenItem span {
    font-size: 2.5vw;
  }
}
@media (max-width: 575.98px) {
  #Projects .contenItem span {
    font-size: 3vw;
  }
}
#Projects .contenItem .img-thumbnail {
  background-color: yellowgreen;
  border-color: yellowgreen !important;
  transition: 0.3s ease-in-out;
}
#Projects .contenItem .description {
  color: #ffffff;
  font-family: "Text";
  background-color: #000000;
  width: 100%;
  text-align: center;
}
#Projects .contenItem .description p {
  text-align: justify;
  font-size: 0.8vw;
}
@media (max-width: 1399.98px) {
  #Projects .contenItem .description p {
    font-size: 1vw;
  }
}
@media (max-width: 1199.98px) {
  #Projects .contenItem .description p {
    font-size: 1.2vw;
  }
}
@media (max-width: 991.98px) {
  #Projects .contenItem .description p {
    font-size: 1.5vw;
  }
}
@media (max-width: 767.98px) {
  #Projects .contenItem .description p {
    font-size: 2vw;
  }
}
@media (max-width: 575.98px) {
  #Projects .contenItem .description p {
    font-size: 3vw;
  }
}
#Projects .contenItem .contenItemLogo {
  border-radius: 5px;
  margin-right: 5px;
}
#Projects .contenItem:hover b {
  color: yellow;
}
@media (max-width: 575.98px) {
  #Projects .contenItem:hover b {
    font-size: 4vw;
  }
}
#Projects .contenItem:hover .img-thumbnail {
  background-color: yellow;
  border-color: yellow !important;
}/*# sourceMappingURL=Projects.css.map */