#Skills{
    background-color: #000000;
    min-height: 100vh;

    h2{
        color: rgba(250, 250, 250, 0.1);
        font-size: 14vw;
        font-family: Handwriting;
        text-align: right;

        @media (max-width: 1399.98px) {
            font-size: 14vw;
        }
        @media (max-width: 1199.98px) {
            font-size: 18vw;
        }
        @media (max-width: 991.98px) {
            font-size: 22vw;
        }
        @media (max-width: 767.98px) {
            font-size: 24vw;
            text-align: left;
        }
        @media (max-width: 575.98px) {
            font-size: 24vw;
            text-align: left;
        }
    }

    .skillsText{
        position:relative;
        top: -20vh;
        left: 0;
        width: 140vh;
        font-size: 3vh;
        text-align: justify;
        color: #ffffff;
        font-family: Text;

        span{
            font-weight: bold;
            color: greenyellow;
            font-style: italic;
        }
        @media (max-width: 1399.98px) {
            font-size: 2vh;
            top: -14vh;
        }
        @media (max-width: 1199.98px) {
            font-size: 2vh;
            top: -14vh;
        }
        @media (max-width: 991.98px) {
            font-size: 2vh;
            top: -16vh;
        }
        @media (max-width: 767.98px) {
            font-size: 2vh;
            top: -2vh;
        }
        @media (max-width: 575.98px) {
            font-size: 2vh;
            top: -2vh;
        }
    }

    .skill{
        text-align: center;
        color: #ffffff;
        font-family: "Text";

        @media (max-width: 575.98px) {
            font-size: 3vw;
        }
        
        h3{
            font-family: "Titles";
            font-size: 2vw;
            color: yellowgreen;
            margin: 0;
            text-align: center;

            @media (max-width: 575.98px) {
                font-size: 4vw;
            }
        }
    }

    .contenItemLogo{
        border-radius: 5px;
        margin-right: 5px;

        @media (max-width: 991.98px) {
            width: 30px;
        }
        @media (max-width: 767.98px) {
            width: 30px;
        }
        @media (max-width: 575.98px) {
            width: 20px;
        }
    }

    .content{
        #SkillsButton{
            position: absolute;
            width: 100px;
            left: 50%;
            margin-left: -50px;
        }
    }
}