#Games{
    background-color: #000000;
    min-height: 100vh;

    h2{
        font-size: 10vw;
        font-family: Handwriting;
        color: rgba(255,255,255,0.1);
        text-align: right;

        @media (max-width: 991.98px) {
            font-size: 12vw;
        }
        @media (max-width: 767.98px) {
            font-size: 16vw;
        }
        @media (max-width: 575.98px) {
            font-size: 16vw;
            text-align: left;
        }
    }

    .gamesText{
        position:relative;
        font-family: "Text";
        top: -10vh;
        left: 0;
        width: 120vh;
        font-size: 4vh;
        text-align: justify;
        color: #ffffff;

        span{
            font-weight:bold !important;
            color: yellowgreen;

        }

        @media (max-width: 1399.98px) {
            font-size: 2vw;
            top: -7vh;
            width: 80vh;
        }
        @media (max-width: 1199.98px) {
            font-size: 2vw;
            top: -5vh;
            width: 70vh;
        }
        @media (max-width: 991.98px) {
            font-size: 2.5vw;
            top: -5vh;
            width: 70vh;
        }
        @media (max-width: 767.98px) {
            font-size: 3vw;
            top: -5vh;
            width: 70vh;
        }
        @media (max-width: 575.98px) {
            font-size: 4vw;
            top: -2vh;
        }
    }

    .item{
        h3{
            font-family: SubTitles;
            font-size: 2vw;
            text-align: center;
            color: #00dbde;
        }

        ul{
            list-style:disc;
            color: yellowgreen;
        }

        p{
            text-align: left;
            font-family: "Text";
            color: #ffffff;
            margin: 0;

            @media (max-width: 575.98px) {
                font-size: 3vw;
            }
        }
    }

    .content{
        #GamesButton{
            position: absolute;
            width: 150px;
            left: 50%;
            margin-left: -75px;
        }
    }
}