footer {
  background-color: #000000;
  color: #ffffff;
  text-align: center;
  font-family: Text;
  padding: 15px;
}
footer ul {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}
footer ul li {
  display: inline-block;
}
footer ul li a {
  text-decoration: none;
  color: #ffffff;
  padding-left: 15px;
  padding-right: 15px;
}
footer ul li a:hover {
  color: greenyellow;
  text-shadow: 0 0 1px greenyellow;
}
footer .copy {
  font-size: 0.7em;
}/*# sourceMappingURL=Footer.css.map */