#Projects {
  //background-color: yellowgreen;
  min-height: 100vh;
  font-family: "Text";
  padding-left: 15px;
  padding-right: 15px;

  h2 {
    color: yellowgreen;
    font-size: 10vw;
    font-family: "Handwriting";

    @media (max-width: 575.98px) {
      font-size: 22vw;
    }
  }

  .contenItem {
    background-color: #000000;
    border-radius: 10px;
    padding: 10px;
    position: relative;

    b {
      color: yellowgreen;
      font-size: 1vw;
      font-family: "SubTitles";
      transition: 0.3s ease-in-out;

      @media (max-width: 1399.98px) {
        font-size: 1.5vw;
      }
      @media (max-width: 1199.98px) {
        font-size: 1.5vw;
      }
      @media (max-width: 991.98px) {
        font-size: 2vw;
      }
      @media (max-width: 767.98px) {
        font-size: 4vw;
      }
      @media (max-width: 575.98px) {
        font-size: 4vw;
      }
    }
    span {
      color: #ffffff;
      font-size: 0.8vw;

      @media (max-width: 1399.98px) {
        font-size: 1vw;
      }
      @media (max-width: 1199.98px) {
        font-size: 1.2vw;
      }
      @media (max-width: 991.98px) {
        font-size: 2vw;
      }
      @media (max-width: 767.98px) {
        font-size: 2.5vw;
      }
      @media (max-width: 575.98px) {
        font-size: 3vw;
      }
    }
    .img-thumbnail {
      background-color: yellowgreen;
      border-color: yellowgreen !important;
      transition: 0.3s ease-in-out;
    }

    .description {
      color: #ffffff;
      font-family: "Text";
      background-color: #000000;
      width: 100%;
      text-align: center;

      p{
        text-align: justify;
        font-size: 0.8vw;

        @media (max-width: 1399.98px) {
          font-size: 1vw;
        }
        @media (max-width: 1199.98px) {
          font-size: 1.2vw;
        }
        @media (max-width: 991.98px) {
          font-size: 1.5vw;
        }
        @media (max-width: 767.98px) {
          font-size: 2vw;
        }
        @media (max-width: 575.98px) {
          font-size: 3vw;
        }
      }
    }

    .contenItemLogo{
        border-radius: 5px;
        margin-right: 5px;
    }

    &:hover {
      b {
        color: yellow;

        @media (max-width: 575.98px) {
          font-size: 4vw;
        }
      }
      .img-thumbnail {
        background-color: yellow;
        border-color: yellow !important;
      }
    }
  }
}
