footer{
    background-color: #000000;
    color: #ffffff;
    text-align: center;
    font-family: Text;
    padding: 15px;

    ul{
        list-style: none;
        margin: 0;
        padding: 0;
        width: 100%;
        li{
            display: inline-block;
            a{
                text-decoration: none;
                color: #ffffff;
                padding-left: 15px;
                padding-right: 15px;

                &:hover{
                    color: greenyellow;
                    text-shadow: 0 0 1px greenyellow;
                }
            }
        }
    }
    .copy{
        font-size: 0.7em;
    }
}